import { POSE_LANDMARKS } from "@mediapipe/pose";

export function createSensorClient() {
  var client = new WebSocket('ws://127.0.0.1:8181/');

  // Connection opened
  client.addEventListener('open', function (event) {
    console.log("open websocket")
  });

  // Connection opened
  client.addEventListener('error', function (event) {
    console.log("error websocket")
    console.log(event)
  });

  // Connection opened
  client.addEventListener('close', function (event) {
    console.log("close websocket")
    console.log(event)
    client = new WebSocket('ws://127.0.0.1:8181/');
  });

  client.updatePose = async function (pose) {
    var poseZero = pose[0]
    poseZero.type = "pose_landmark"
    try {
      while (this.bufferedAmount > 1000) {
        //sleep wait for budder
        console.log("buffer overflow wait second")
        await sleep(5)
      }
      if (client.readyState == 1) {
        client.send(JSON.stringify(poseZero))
      } else if (client.readyState == 3) {
        console.log("websocket state to close and recreate")
        client = new WebSocket('ws://127.0.0.1:8181/');
      }
    } catch (e) {
      //error
    }
  }

  function sleep(ms) {
    return new Promise(resolve => setTimeout(resolve, ms));
  }
  return client
}
